<template>
	<div id="app">
		<div class="center-api-text">
			<Loading v-show="loading">
				<vue-loading type="barsCylon" color="#99892b" :size="{ width: '150px', height: '150px' }"></vue-loading>    
			</Loading>
			<p v-if="items.user">{{ items.user }} / Lv{{ items.lv }}</p>
			<p>first-card / draw-card</p> 
			<div class="tarot-card-list">
				<p v-if="items.first">
					<img v-show="!loading" :src="'https://syui.cf/ai/tarot/tarot_' + ('00' + this.items.first).slice(-2) + '.gif'" @error="onErrorfirst"/> <span class="draw-card"> <img v-show="!loading" :src="'https://syui.cf/ai/tarot/tarot_' + ('00' + this.items.draw).slice(-2) + '.gif'" @error="onError"/> </span>
			</p>
		</div>
	</div>
</div>
</template>

<script>
import axios from 'axios'
import { VueLoading } from 'vue-loading-template';
let url = "https://cardai-cros.herokuapp.com/https://api.syui.cf/users/1";
export default {
	data() {
		return {
			items: null,
			loading: true
		}
	},
	mounted() {
			axios
				.get(url)
				.then(response => { 
					this.items = response.data;
				}).finally(() => this.loading = false);
	},
	components: {
		VueLoading
	},
	methods: {
		onError (e) {
			if (this.items !== null) {
				e.target.src = 'https://syui.cf/ai/tarot/tarot_' + ('00' + this.items.draw).slice(-2) + '.webp'
			}
		},
		onErrorfirst (e) {
			e.target.src = 'https://syui.cf/ai/tarot/tarot_' + ('00' + this.items.first).slice(-2) + '.webp'
		}
	}
}
</script>

<style>
	.center-api-text {
		text-align: center;
	}

@media screen and (min-width:1000px) { 
	.center-api-text img {
		width: 320px !important;
	}

	.containerx p img {
		padding: 0 !important;
	}
}
</style>
